.nav {
  display: flex;
  list-style: none;
  align-items: center;
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
}
.nav__actions {
  font-size: 0.75rem;
  text-transform: uppercase;
}
.nav__actions--middle {
  margin-left: 2.5rem;
}
.nav__actions a {
  text-decoration: none;
  color: black;
}
.nav__actions a .lupa {
  width: 1.875rem;
}
.nav__actions--search {
  margin-left: auto;
}
.nav__logotext {
  font-weight: 600;
  font-size: 1.25rem;
  margin-right: auto;
  text-transform: capitalize;
}
.nav__logo {
  margin-right: 0.625rem;
}

.heading {
  max-width: 69.375rem;
  margin: 0 auto;
}

.hero {
  display: flex;
}

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-size: 100%;
  font-family: "Playfair Display";
}

img {
  display: block;
  max-width: 100%;
}